@font-face {
    font-family: 'Nekst-Black';
    src: url('./assets/Nekst-Medium.ttf');
}

@font-face {
    font-family: 'Gilroy';
    src: url('./assets/Gilroy-Regular.ttf');
}

* {
    margin: 0;
    padding: 0;
}

html {
    width: 100%;
    height: 100%;
    min-width: 100%;
    min-height: 100%;
}

body {
    min-height: 100vh;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

body::-webkit-scrollbar {
    width: 0px;
}